import { GoogleMap, useLoadScript } from '@react-google-maps/api';
// import { useDispatch } from 'react-redux';
// import { getFlares } from '../../store/actions/flare';

const defaultZoom = 16;
const defaultCenter = { lat: 32.885, lng: 13.188 };
const defaultMapContainerStyle = { width: '100%', height: '100%' };

const MapContainer = ({
  zoom = defaultZoom,
  center = defaultCenter,
  mapContainerStyle = defaultMapContainerStyle,
  children,
}) => {
  // const dispatch = useDispatch();
  const { isLoaded, loadError } = useLoadScript({
    googleMapsApiKey: 'AIzaSyCSLXG5j-CqSB0zR476bJFXOSQ9sWuXqH0',
    // googleMapsApiKey: 'AIzaSyA3STsJ4ZCaIz50jARJ1jcxzBH7obM9zi0',
  });

  if (loadError) return 'Error loading maps';
  if (!isLoaded) return 'Loading Maps';

  // const loadFlares = () => {
  //   dispatch(getFlares());
  // };

  return (
    <GoogleMap
      mapContainerStyle={mapContainerStyle}
      zoom={zoom}
      center={center}
      // onLoad={loadFlares}
      options={{
        // zoomControlOptions: { position: 'TOP_RIGHT' },
        streetViewControl: false,
        navigationControl: false,
        zoomControl: false,
        fullscreenControl: false,
      }}
    >
      {children}
    </GoogleMap>
  );
};

export default MapContainer;
