import './Modal.scss';

const Modal = ({ isModalOpen, modalBodyClass, children, close }) => {
  const preventClose = (e) => e.stopPropagation();
  return (
    <main
      className={`${isModalOpen ? 'modal-container' : 'display-none'}`}
      onClick={close}
    >
      <section className={`${modalBodyClass}`} onClick={preventClose}>
        {children}
      </section>
    </main>
  );
};

export default Modal;
