import { MapProvider } from './context/MapState';
import MapComponent from 'components/map/Map';
import FloatingActionButton from 'components/map/FloatingActionButton/FloatingActionButton';
import CreateTicketModal from 'components/map/CreateTicketModal/CreateTicketModal';
import './Map.scss';

const Map = () => {
  return (
    <MapProvider>
      <section className="map-container">
        <MapComponent />
        <FloatingActionButton />
        <CreateTicketModal />
      </section>
    </MapProvider>
  );
};

export default Map;
