import { createContext, useReducer } from 'react';
import MapContextReducer from './MapContextReducer';

const initialState = {
  isCreating: false,
  isModalOpen: false,
};

export const MapContext = createContext(initialState);

export const MapProvider = ({ children }) => {
  const [state, dispatch] = useReducer(MapContextReducer, initialState);

  const open = () => dispatch({ type: 'OPEN_MODAL' });
  const close = () => dispatch({ type: 'CLOSE_MODAL' });
  const create = () => dispatch({ type: 'CREATE_ACTIVE' });
  const disable = () => dispatch({ type: 'CREATE_DISABLED' });

  return (
    <MapContext.Provider
      value={{
        isCreating: state.isCreating,
        isModalOpen: state.isModalOpen,
        open,
        close,
        create,
        disable,
      }}
    >
      {children}
    </MapContext.Provider>
  );
};
