import {
  OPEN_MODAL,
  CLOSE_MODAL,
  CREATE_ACTIVE,
  CREATE_DISABLED,
} from './constants';

const MapContextReducer = (state, action) => {
  switch (action.type) {
    case OPEN_MODAL:
      return {
        ...state,
        isModalOpen: true,
      };
    case CLOSE_MODAL:
      return {
        ...state,
        isModalOpen: false,
      };
    case CREATE_ACTIVE:
      return {
        ...state,
        isCreating: true,
      };
    case CREATE_DISABLED:
      return {
        ...state,
        isCreating: false,
      };
    default:
      return state;
  }
};

export default MapContextReducer;
