import MapContainer from './MapContainer';
import { MapContext } from 'pages/Map/context/MapState';
import { useContext } from 'react';
import { Marker } from '@react-google-maps/api';
import greenTower from 'assets/images/green-tower.svg';
import redTower from 'assets/images/red-tower.svg';
import './Map.scss';

const Map = () => {
  const tempList = [
    {
      latitude: 32.886677,
      longitude: 13.1815405,
      count: 4,
    },
    {
      latitude: 32.888,
      longitude: 13.185,
      count: 2,
    },
    {
      latitude: 32.8865,
      longitude: 13.19175,
      count: 0,
    },
    {
      latitude: 32.881,
      longitude: 13.186,
      count: 10,
    },
  ];

  const { open } = useContext(MapContext);

  const handleDblClick = (zone) => {
    console.log(zone);
    open();
  };

  return (
    <MapContainer>
      {tempList.map((zone, index) => (
        <Marker
          key={index}
          position={{ lat: zone.latitude, lng: zone.longitude }}
          // onClick={selectMarker.bind(this, zone)}
          onDblClick={handleDblClick.bind(this, zone)}
          label={{
            color: '#fff',
            fontWeight: 'bold',
            fontSize: '1rem',
            text: `${zone.count}`,
            className: 'marker-label',
          }}
          icon={zone.count ? redTower : greenTower}
        ></Marker>
      ))}
    </MapContainer>
  );
};

export default Map;
