import { MapContext } from 'pages/Map/context/MapState';
import { useContext } from 'react';
import './FloatingActionButton.scss';
import addCircle from 'assets/images/create-ticket/add-circle.svg';
import crossCricle from 'assets/images/create-ticket/cross-circle.svg';

const FloatingActionButton = () => {
  const { isCreating, create, disable } = useContext(MapContext);
  return (
    <section className="fab-container">
      <article className="fab-main">
        <div className="fab-image-container">
          {isCreating ? (
            <img src={crossCricle} alt="create ticket" onClick={disable} />
          ) : (
            <img src={addCircle} alt="create ticket" onClick={create} />
          )}
        </div>
        {isCreating ? (
          <h4 onClick={disable}>حذف التذكرة</h4>
        ) : (
          <h3 onClick={create}>إنشاء تذكرة</h3>
        )}
      </article>
      {isCreating && (
        <>
          <article className="fab-create-note">
            <div className="fab-image-container">
              {/* <img src={warningSign} alt="warning sign" /> */}
              img
            </div>
            <h6>يمكنك الآن اختيار الفليرات بشكل فردي أو كحزمة مشكلة!</h6>
          </article>
          <button className="fab-proceed-button">متابعة</button>
        </>
      )}
    </section>
  );
};

export default FloatingActionButton;
