import { useContext } from 'react';
import { MapContext } from 'pages/Map/context/MapState';
import Modal from 'layout/reusables/modal/Modal';

import './CreateTicketModal.scss';
import redTower from 'assets/images/red-tower.svg';

const CreateTicketModal = () => {
  const { isModalOpen, close, isCreating } = useContext(MapContext);
  const tempList = [
    {
      count: 35,
      type: 'تذبذب في الشبكة',
    },
    {
      count: 22,
      type: 'انقطاع الخدمة',
    },
    {
      count: 11,
      type: 'مشاكل فوترة',
    },
  ];
  return (
    <Modal isModalOpen={isModalOpen} close={close} modalBodyClass="ctm-body">
      <div className="ctm-content-container">
        <div className="ctm-header-container">
          <article className="ctm-image-container">
            <img src={redTower} alt="red-tower" />
          </article>
          <article>
            <h3>برج صلاح الدين</h3>
            <h6>TP-110</h6>
          </article>
        </div>
        <div className="ctm-flares-container">
          <ul>
            {tempList.map((item, index) => (
              <li key={index}>
                {isCreating && <input type="checkbox" name="checkbox" />}
                <span>({item.count})</span>
                {item.type}
              </li>
            ))}
          </ul>
        </div>
        <div className="ctm-buttons-container">
          <button>إنشاء تذكرة</button>
          <button>استمرار</button>
        </div>
      </div>
    </Modal>
  );
};

export default CreateTicketModal;
