import { NavLink } from 'react-router-dom';
import './Sidenav.scss';
import userLogo from '../../assets/images/create-ticket/user-logo.svg';
import mapLogo from '../../assets/images/create-ticket/map-logo.svg';
import ticketLogo from '../../assets/images/create-ticket/ticket-logo.svg';
import reportsLogo from '../../assets/images/create-ticket/reports-logo.svg';
import notificationsLogo from '../../assets/images/create-ticket/notifications-logo.svg';

const Sidenav = () => {
  return (
    <section>
      <article className="side-nav">
        <main className="nav-info-logo-container__user">
          <section className="nav-logo-container">
            <img src={userLogo} alt="user logo" />
          </section>
          <section className="nav-text-container">
            <h3>محمد أحمد</h3>
            <h6>مشرف</h6>
          </section>
        </main>
        <ul>
          <li>
            <NavLink to="/flares" className="nav-links">
              <section className="nav-info-logo-container">
                <section className="nav-logo-container">
                  <img src={mapLogo} alt="map logo" />
                </section>
                <section className="nav-text-container">
                  <h3>الخريطة</h3>
                </section>
              </section>
            </NavLink>
          </li>
          <li>
            <NavLink to="/flares" className="nav-links">
              <section className="nav-info-logo-container">
                <section className="nav-logo-container">
                  <img src={ticketLogo} alt="map logo" />
                </section>
                <section className="nav-text-container">
                  <h3>قائمة التذاكر</h3>
                </section>
              </section>
            </NavLink>
          </li>
          <li>
            <NavLink to="/flares" className="nav-links">
              <section className="nav-info-logo-container">
                <section className="nav-logo-container">
                  <img src={notificationsLogo} alt="map logo" />
                </section>
                <section className="nav-text-container">
                  <h3>الإشعارات</h3>
                </section>
              </section>
            </NavLink>
          </li>
          <li>
            <NavLink to="/flares" className="nav-links">
              <section className="nav-info-logo-container">
                <section className="nav-logo-container">
                  <img src={reportsLogo} alt="map logo" />
                </section>
                <section className="nav-text-container">
                  <h3>التقارير</h3>
                </section>
              </section>
            </NavLink>
          </li>
        </ul>
        <NavLink to="/login" className="nav-links">
          <section className="nav-info-logo-container">
            <section className="nav-logo-container">
              {/* <img src={reportsLogo} alt="map logo" /> */}
              img
            </section>
            <section className="nav-text-container">
              <h4>تسجيل الخروج</h4>
            </section>
          </section>
        </NavLink>
      </article>
    </section>
  );
};

export default Sidenav;
